.page-wrapper-home {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  overflow: scroll;
  background-color: #282a36;
}
.content-wrapper-home {
  display: flex;
  min-width: 85vw;
  min-height: 85vh;
  position: relative;

  background-color: white;
}

.button_containerr {

/*  flex-wrap: wrap;*/
/*  margin: 6px;*/
/*  gap: 12px;*/
display: grid;
grid-auto-flow: column;
grid-column-gap: 10px;


/*  background-color: violet;*/
} 


input {
  display: none;
}

#section-1, #section-2 {
  display: flex;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

#section-1 {
  opacity: 1;
  transition: transform 1s, opacity 1s;
}

#section-2 {
  opacity: 0;
  transition: transform 1s, opacity 1s;
}

.image-container {
  width: 50%;
  height: 100%;
  background-color: lightblue;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

img {
  flex-shrink: 0;
    min-width: 100%;
    min-height: 100%;
}



.info-container-home {
  width: 100%;
  height: 100%;
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  
  padding-left: 20px;
  padding-right: 20px;
  overflow-wrap: break-word;

  background-color: #282a36;
}



#section-1 .info-container {
  background-color: lightcyan;
}

#section-2 .info-container {
  background-color: lavender;
}



label.home {
  display: block;
  border: solid 2px;
  border-color: #7EAAC7;
  padding: 1em 2em;
  border-radius: 5px;
  cursor: pointer;
  color:#E5E9F0;

}
label.home:hover, input:hover+label.home, label.home:active, input:active+label.home {
    background: #7EAAC7;
}

#toggle2:checked ~ #section-2 {
  opacity: 1;
}

#toggle2:checked ~ #section-1 {
  opacity: 0;
/*  transform: translateX(14em);*/
  pointer-events: none;
}



