.page-wrapper-all {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  overflow: scroll;
  background-color: #282a36;
}
.content-wrapper-all {
  display: flex;
  min-width: 85vw;
  min-height: 85vh;
  position: relative;

  background-color: #282a36;
}

.button_container_all {

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
/*  align-items: center;*/
  justify-content: space-evenly;
  align-content:space-between;
  width: 85vw;
  height: 85vh;
  position: relative;
  row-gap: 10vh;

/*  background-color: violet;*/
} 


input {
  display: none;
}



.info-container-all {
  width: 100%;
  height: 100%;
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  
  padding-left: 20px;
  padding-right: 20px;
  overflow-wrap: break-word;

  background-color: #282a36;
}



label.all {
  display: block;
  border: solid 2px;
  border-color: #6272a4;
  padding: 1em 2em;
  border-radius: 5px;
  cursor: pointer;
  color:#E5E9F0;

}
label.all:hover, input:hover+label.all, label.all:active, input:active+label.all {
    background: #282a36;
/*    border: solid 3px;*/
    border-color: #bd93f9;
    color: #f8f8f2;

}

